<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="flex flex-wrap-reverse items-center mb-base">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
      </div>

      <div id="loading-container" class="nv-row row hover vs-con-loading__container">
        <div class="nv-col w-full">
          <p class="info-row">
            <span>{{ $t('ID') }}:</span>
            <span>{{ workflowInstance.instanceId | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('WORKFLOW_NAME') }}:</span>
            <span>{{ workflowInstance.workflowName | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('WORKFLOW_TYPE') }}:</span>
            <span>{{ workflowInstance.workflowTypeName | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('STATUS') }}:</span>
            <span>{{ workflowInstance.statusDescription | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('CREATION_DATE') }}:</span>
            <span>{{ workflowInstance.creationDate | formatUnixDate }}</span>
          </p>
        </div>
      </div>
    </nv-card>

    <div class="mt-base">
      <template v-if="workflowInstance.workflowTypeId === 'WF_BEN'">
        <beneficiary-workflow-instance-view :instanceId="workflowInstance.instanceId" viewOnly></beneficiary-workflow-instance-view>
      </template>
      <template v-if="workflowInstance.workflowTypeId === 'WF_FT'">
        <funds-transfer-workflow-instance-view :instanceId="workflowInstance.instanceId" viewOnly></funds-transfer-workflow-instance-view>
      </template>
      <template v-if="workflowInstance.workflowTypeId === 'WF_RPT'">
        <report-workflow-instance-view :instanceId="workflowInstance.instanceId" viewOnly></report-workflow-instance-view>
      </template>
    </div>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import WorkflowTaskTableMixin from '@/mixins/commons/WorkflowTaskTableMixin'
import WorkflowService from '@/services/commons/WorkflowService'
import BeneficiaryWorkflowInstanceView from '@/views/beneficiary/beneficiary-workflow-instance/BeneficiaryWorkflowInstanceView'
import FundsTransferWorkflowInstanceView from '@/views/ft/funds-transfer-workflow-instance/FundsTransferWorkflowInstanceView'
import ReportWorkflowInstanceView from '@/views/report/report-workflow-instance/ReportWorkflowInstanceView'

export default {
  name: 'workflow-request-view',
  mixins: [AlertMixin, LoadingMixin, WorkflowTaskTableMixin],
  components: {
    BeneficiaryWorkflowInstanceView,
    FundsTransferWorkflowInstanceView,
    ReportWorkflowInstanceView
  },
  data: function () {
    return {
      workflowInstance: {}
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('loading-container')
      WorkflowService.workflowInstances({
        instanceId: this.$route.params.id
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.workflowInstance = response.data.workflowInstanceList[0] || {}
        self.loadTaskTableData('tasks-loading-container')
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
